<template>
    <div class="register container">
        <div class="info">
            <Tile :display="true"></Tile>
            <Step step="3"></Step>
            <div class="form">
                <table>
                    <tr>
                        <td class="tile"><p>企业名称<span style="width: 100%;display: inline-block"></span></p></td>
                        <td class="input">
                            <label>
                                <input type="text" placeholder="请输入企业全称" v-model="form.company_name">
                            </label>
                        </td>
                    </tr>
                    <tr>
                        <td class="tile"><p>所属行业<span style="width: 100%;display: inline-block"></span></p></td>
                        <td class="input">
                            <div class="hack-input" :class="{focus:set_trade}" @click.self="setTrade()">
                                {{form.trade}}
                                <div class="choose-trade row" v-show="set_trade">
                                    <div class="col-4">
                                        <ul>
                                            <li v-for="(i,k) in industry" :key="k" @click="seletOne(k)">{{i.name}}</li>
                                        </ul>
                                    </div>
                                    <div class="col-4">
                                        <ul>
                                            <li v-for="(ii,kk) in industry[selet_one].child" @click="seletTwo(kk) ">
                                                {{ii.name}}
                                            </li>
                                        </ul>
                                    </div>
                                    <div class="col-4">
                                        <ul>
                                            <li v-for="(iii,kkk) in industry[selet_one].child[selet_two].child"
                                                @click="insertIndustry(iii)">{{iii}}
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </td>
                    </tr>
                    <tr>
                        <td class="tile"><p>企业规模<span style="width: 100%;display: inline-block"></span></p></td>
                        <td class="input">
                            <label>
                                <input type="text" placeholder="企业规模大小" v-model="form.employee_num">
                            </label>
                        </td>
                    </tr>
                    <tr>
                        <td class="tile"><p>联系人<span style="width: 100%;display: inline-block"></span></p></td>
                        <td class="input">
                            <label>
                                <input type="text" placeholder="请输入联系人姓名" v-model="form.contact">
                            </label>
                        </td>
                    </tr>
                    <tr>
                        <td class="tile"><p>职位<span style="width: 100%;display: inline-block"></span></p></td>
                        <td class="input">
                            <label>
                                <input type="text" placeholder="请输入联系人职位" v-model="form.job">
                            </label>
                        </td>
                    </tr>
                </table>
                <div class="btn" @click="sign">
                    立即注册
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Tile from '../components/title'
    import Step from '../components/step'
    import {mapState} from 'vuex'
    import {industry} from '../assets/result'

    export default {
        name: "step-two",
        components: {
            Tile,
            Step
        },
        data() {
            return {
                set_trade: false,
                set_employee: false,
                form: {
                    company_name: '',
                    trade: '请选择行业',
                    employee_num: '',
                    contact: '',
                    job: ''
                },
                industry: industry,
                selet_one: 0,
                selet_two: 0,
                selet_three: 0
            }
        },
        created() {
            if (this.register.step !== 3) {
                this.$router.push('/')
            }
        },
        methods: {
            setTrade() {
                this.set_trade = !this.set_trade
            },
            seletOne(k) {
                this.selet_two = 0;
                this.selet_three = 0;
                this.selet_one = k;
            },
            seletTwo(k) {
                this.selet_three = 0;
                this.selet_two = k
            },
            insertIndustry(kkk) {
                this.form.trade = kkk;
                this.set_trade = false
            },
            sign() {
                let data = {};
                data.phone = this.register.phone;
                data.pawrd = this.register.password;
                data.username = this.register.user;
                data.business_name = this.form.company_name;
                data.extra = JSON.stringify(this.form);
                for (let i in data){
                    if (data[i] ==''){
                        switch (i){
                            case 'phone':
                                this.bus.$emit('notice','手机号不正确');
                                return
                            case 'pawrd':
                                this.bus.$emit('notice','密码未设置');
                                return
                            case 'username':
                                this.bus.$emit('notice','用户名未设置');
                                return
                            case 'business_name':
                                this.bus.$emit('notice','企业名称未填写');
                                return
                            case 'extra':
                                this.bus.$emit('notice','其他字段未填写');
                                return
                        }


                    }
                }
                this.yum('company/login/register', data).then(res => {
                    if (res.data.status === 200) {
                        this.$router.push('/success')
                    } else {
                        alert(res.data.msg)
                    }
                })
            }
        },
        computed: {
            ...mapState('register', ['register'])
        }
    }
</script>

<style scoped lang="stylus">
    .register
        height 970px

    .info
        width 800px
        margin 50px auto 0
        padding 0 50px 50px
        background-color white
        display block
        .form
            width 550px
            margin 60px auto 0
            table
                width 520px
                border-collapse separate
                border-spacing 20px 25px
                tr
                    height 50px
                    line-height 50px
                    td
                        height 50px
                        line-height 50px
                        &.tile
                            width 90px
                            font-size 16px
                            color black
                            font-weight bold
                            p
                                height 50px
                                line-height 50px
                                text-align justify
                        &.input
                            width 420px
                            position relative
                            div.hack-input
                                display block
                                height 50px
                                line-height 50px
                                border-radius 4px
                                border 1px solid #999
                                padding 0 10px
                                user-select none
                                &.focus
                                    box-shadow 0 0 5px #0089ff
                                    border-color #0089ff
                                    &:after
                                        transform rotate(180deg)
                                        transform-origin 50% 20%
                                &::after
                                    display inline-block
                                    content ''
                                    width 0
                                    height 0
                                    border-bottom 5px solid #fff
                                    border-top 5px solid #555
                                    border-left 5px solid #fff
                                    border-right 5px solid #fff
                                    position absolute
                                    right 10px
                                    top 23px
                                .choose-trade
                                    position absolute
                                    width 500px
                                    line-height 30px
                                    background-color white
                                    z-index 1
                                    left 0
                                    top 60px
                                    border 1px solid #999
                                    box-shadow 1px 2px 6px
                                    ul
                                        height 180px
                                        overflow-x hidden
                                        padding 0 10px
                                    li:hover
                                        background-color #c1c1c1
                            input
                                height 50px
                                line-height 50px
                                width 100%
                                padding 0 10px
                                outline none
                                border-radius 4px
                                border 1px solid #999
                                &:focus
                                    box-shadow 0 0 5px #0089ff
                                    border-color #0089ff
                            img.access
                                display: inline-block
                                position: absolute
                                right: -25px
                                top: 17px
                            span.fail
                                position absolute
                                display inline-block
                                height 16px
                                line-height 16px
                                font-size 12px
                                color red
                                bottom -20px
                                left 0
                                animation debounce 0.1s 3
                            @keyframes debounce {
                                from {
                                    bottom -22px
                                }
                                to {
                                    bottom -20px
                                }
                            }
        .btn
            width 378px
            height 60px
            line-height 60px
            margin 40px 120px 0
            text-align center
            font-size 18px
            background-color #0089ff
            color white
            cursor pointer
            border-radius 6px
            &:hover
                background-color #33a0ff
</style>